.svg-container {
  width: 100%; height: 100%; transform: translate3d(0px, 0px, 0px);
}
.png0 {
  animation: png0 2s ease-in-out infinite alternate;
}
@keyframes png0 {
  0%{
    transform: matrix(0.9072231078147888, 0, 0, 1, 99.53121948242188, 407.97900390625);
  }
  100%{
    transform: matrix(1, 0, 0, 1, 90.53121948242188, 407.97900390625);
  }
}
.png1 {
  animation: png1 3s ease-in-out infinite alternate;
}
@keyframes png1 {
